import JSZip from 'jszip';

export class CoverExtractor {
  static async extractCover(fileData) {
    try {
      console.log(' EPUB   ...');
      
      // ArrayBuffer 
      let buffer;
      if (fileData instanceof ArrayBuffer) {
        buffer = fileData;
      } else if (typeof fileData === 'string' && fileData.startsWith('data:')) {
        const base64Data = fileData.split(',')[1];
        const binaryString = atob(base64Data);
        buffer = new ArrayBuffer(binaryString.length);
        const view = new Uint8Array(buffer);
        for (let i = 0; i < binaryString.length; i++) {
          view[i] = binaryString.charCodeAt(i);
        }
      } else {
        throw new Error('   ');
      }

      // ZIP  
      const zip = new JSZip();
      const zipContent = await zip.loadAsync(buffer);
      
      // 1. container.xml OPF   
      const containerXml = await zipContent.files['META-INF/container.xml']?.async('string');
      if (!containerXml) {
        throw new Error('container.xml   ');
      }

      const rootfileMatch = containerXml.match(/full-path="([^"]+)"/);
      if (!rootfileMatch) {
        throw new Error('OPF     ');
      }

      const opfPath = rootfileMatch[1];
      console.log(' OPF  :', opfPath);

      // 2. OPF    
      const opfContent = await zipContent.files[opfPath]?.async('string');
      if (!opfContent) {
        throw new Error('OPF    ');
      }

      // OPF   
      const opfDir = opfPath.substring(0, opfPath.lastIndexOf('/') + 1);

      //    
      const coverMethods = [
        //  1: meta name="cover" 
        () => {
          const coverMetaMatch = opfContent.match(/<meta\s+name="cover"\s+content="([^"]+)"/i);
          if (coverMetaMatch) {
            const coverId = coverMetaMatch[1];
            const itemMatch = opfContent.match(new RegExp(`<item\\s+[^>]*id="${coverId}"[^>]*href="([^"]+)"`));
            if (itemMatch) {
              return opfDir + itemMatch[1];
            }
          }
          return null;
        },

        //  2: properties="cover-image" 
        () => {
          const coverMatch = opfContent.match(/<item\s+[^>]*properties="cover-image"[^>]*href="([^"]+)"/i);
          if (coverMatch) {
            return opfDir + coverMatch[1];
          }
          return null;
        },

        //  3:    
        () => {
          const commonNames = ['cover.jpg', 'cover.jpeg', 'cover.png', 'cover.gif', 'cover.webp'];
          for (const name of commonNames) {
            const fullPath = opfDir + name;
            if (zipContent.files[fullPath]) {
              return fullPath;
            }
          }
          return null;
        },

        //  4:     
        () => {
          const imageMatch = opfContent.match(/<item\s+[^>]*href="([^"]+\.(jpg|jpeg|png|gif|webp))"/i);
          if (imageMatch) {
            return opfDir + imageMatch[1];
          }
          return null;
        }
      ];

      //     
      let coverPath = null;
      for (const method of coverMethods) {
        coverPath = method();
        if (coverPath && zipContent.files[coverPath]) {
          console.log('   :', coverPath);
          break;
        }
      }

      if (!coverPath || !zipContent.files[coverPath]) {
        console.warn('     ');
        return null;
      }

      // 3.   
      const imageFile = zipContent.files[coverPath];
      const imageArrayBuffer = await imageFile.async('arraybuffer');
      
      // 4.  
      const thumbnail = await this.createThumbnail(imageArrayBuffer, coverPath);
      
      console.log('   ');
      return thumbnail;

    } catch (error) {
      console.error('   :', error);
      return null;
    }
  }

  static async createThumbnail(imageArrayBuffer, originalPath) {
    return new Promise((resolve, reject) => {
      //   (10)
      const timeout = setTimeout(() => {
        console.error('   ');
        resolve(null);
      }, 10000);

      try {
        //   
        if (imageArrayBuffer.byteLength > 10 * 1024 * 1024) { // 10MB 
          console.warn('    :', imageArrayBuffer.byteLength);
          clearTimeout(timeout);
          resolve(null);
          return;
        }

        // Blob 
        const blob = new Blob([imageArrayBuffer], { 
          type: this.getMimeType(originalPath)
        });
        
        // URL 
        const url = URL.createObjectURL(blob);
        
        // Image  
        const img = new Image();
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');

        //     
        let isHandled = false;

        //   
        const handleLoad = () => {
          if (isHandled) return;
          isHandled = true;
          
          clearTimeout(timeout);
          URL.revokeObjectURL(url);
          
          try {
            //   
            if (img.width === 0 || img.height === 0) {
              console.error('    ');
              resolve(null);
              return;
            }
            
            //    ( 220x300,  )
            const maxWidth = 220;
            const maxHeight = 300;
            let { width, height } = img;

            if (width > maxWidth || height > maxHeight) {
              const ratio = Math.min(maxWidth / width, maxHeight / height);
              width = Math.floor(width * ratio);
              height = Math.floor(height * ratio);
            }

            //   
            if (width < 1) width = 1;
            if (height < 1) height = 1;

            //   
            canvas.width = width;
            canvas.height = height;

            //  
            ctx.drawImage(img, 0, 0, width, height);

            // Base64  (JPEG,  0.8)
            const thumbnailDataUrl = canvas.toDataURL('image/jpeg', 0.8);
            
            console.log('  :', `${width}x${height}`);
            resolve(thumbnailDataUrl);

          } catch (canvasError) {
            console.error('   :', canvasError);
            resolve(null);
          }
        };

        const handleError = () => {
          if (isHandled) return;
          isHandled = true;
          
          clearTimeout(timeout);
          URL.revokeObjectURL(url);
          console.error('   ');
          resolve(null);
        };

        //   
        img.onload = handleLoad;
        img.onerror = handleError;

        //   
        img.crossOrigin = 'anonymous';

        //  
        img.src = url;

      } catch (error) {
        clearTimeout(timeout);
        console.error('   :', error);
        resolve(null);
      }
    });
  }

  static getMimeType(filePath) {
    const ext = filePath.toLowerCase().split('.').pop();
    const mimeTypes = {
      'jpg': 'image/jpeg',
      'jpeg': 'image/jpeg',
      'png': 'image/png',
      'gif': 'image/gif',
      'webp': 'image/webp'
    };
    return mimeTypes[ext] || 'image/jpeg';
  }
} 