/**
 * IndexedDB   E-book  
 * localStorage    GB   
 */

class IndexedDBStorage {
  static instance = null;
  
  constructor() {
    this.dbName = 'EReaderDB';
    this.version = 1;
    this.db = null;
    this.isInitializing = false;
    this.initPromise = null;
    
    // Object Store 
    this.stores = {
      books: 'books',           //  
      bookFiles: 'bookFiles',   //    ( )
      settings: 'settings',     //  
      highlights: 'highlights', // 
      chats: 'chats'           // AI  
    };
  }

  /**
   *   
   */
  static getInstance() {
    if (!IndexedDBStorage.instance) {
      IndexedDBStorage.instance = new IndexedDBStorage();
    }
    return IndexedDBStorage.instance;
  }

  /**
   *    IndexedDB 
   */
  async init(timeoutMs = 5000) {
    //      
    if (this.isInitializing && this.initPromise) {
      return this.initPromise;
    }

    //    
    if (this.db) {
      return this.db;
    }

    this.isInitializing = true;
    
    this.initPromise = new Promise((resolve, reject) => {
      let resolved = false;
      
      //  
      const timeout = setTimeout(() => {
        if (!resolved) {
          resolved = true;
          this.isInitializing = false;
          console.error(' IndexedDB  ');
          reject(new Error('IndexedDB  '));
        }
      }, timeoutMs);

      try {
        const request = indexedDB.open(this.dbName, this.version);
        
        request.onerror = () => {
          if (!resolved) {
            resolved = true;
            clearTimeout(timeout);
            this.isInitializing = false;
            console.error(' IndexedDB  :', request.error);
            reject(request.error);
          }
        };
        
        request.onsuccess = () => {
          if (!resolved) {
            resolved = true;
            clearTimeout(timeout);
            this.db = request.result;
            this.isInitializing = false;
            resolve(this.db);
          }
        };
        
        request.onupgradeneeded = (event) => {
          try {
            const db = event.target.result;
            
            //   
            if (!db.objectStoreNames.contains(this.stores.books)) {
              const booksStore = db.createObjectStore(this.stores.books, { keyPath: 'id' });
              booksStore.createIndex('title', 'title', { unique: false });
              booksStore.createIndex('author', 'author', { unique: false });
              booksStore.createIndex('addedDate', 'addedDate', { unique: false });
            }
            
            //     ( )
            if (!db.objectStoreNames.contains(this.stores.bookFiles)) {
              db.createObjectStore(this.stores.bookFiles, { keyPath: 'bookId' });
            }
            
            //  
            if (!db.objectStoreNames.contains(this.stores.settings)) {
              db.createObjectStore(this.stores.settings, { keyPath: 'id' });
            }
            
            //  
            if (!db.objectStoreNames.contains(this.stores.highlights)) {
              const highlightsStore = db.createObjectStore(this.stores.highlights, { keyPath: 'id' });
              highlightsStore.createIndex('bookId', 'bookId', { unique: false });
            }
            
            //  
            if (!db.objectStoreNames.contains(this.stores.chats)) {
              const chatsStore = db.createObjectStore(this.stores.chats, { keyPath: 'id' });
              chatsStore.createIndex('bookId', 'bookId', { unique: false });
            }
            
          } catch (upgradeError) {
            console.error('   :', upgradeError);
            if (!resolved) {
              resolved = true;
              clearTimeout(timeout);
              this.isInitializing = false;
              reject(upgradeError);
            }
          }
        };
        
        request.onblocked = () => {
          console.warn(' IndexedDB  -   DB  ');
        };
        
      } catch (initError) {
        if (!resolved) {
          resolved = true;
          clearTimeout(timeout);
          this.isInitializing = false;
          console.error(' IndexedDB   :', initError);
          reject(initError);
        }
      }
    });

    try {
      const result = await this.initPromise;
      return result;
    } finally {
      this.isInitializing = false;
    }
  }

  /**
   * DB     
   */
  async ensureConnection() {
    if (!this.db) {
      try {
        await this.init(3000); // 3 
      } catch (error) {
        console.error(' DB  :', error);
        throw new Error('IndexedDB  ');
      }
    }
  }

  /**
   *   
   */
  async getTransaction(storeNames, mode = 'readonly') {
    await this.ensureConnection();
    
    if (!this.db) {
      throw new Error('IndexedDB  .');
    }
    
    try {
      return this.db.transaction(storeNames, mode);
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }

  /**
   *   
   */
  async saveBookMetadata(book) {
    const transaction = await this.getTransaction([this.stores.books], 'readwrite');
    const store = transaction.objectStore(this.stores.books);
    
    //     
    const { fileData, content, ...metadata } = book;
    const bookMetadata = {
      ...metadata,
      hasFileData: !!(fileData || content),
      savedAt: new Date().toISOString()
    };
    
    return new Promise((resolve, reject) => {
      const request = store.put(bookMetadata);
      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
  }

  /**
   *     ()
   */
  async saveBookFile(bookId, fileData) {
    const transaction = await this.getTransaction([this.stores.bookFiles], 'readwrite');
    const store = transaction.objectStore(this.stores.bookFiles);
    
    const bookFile = {
      bookId,
      fileData,
      savedAt: new Date().toISOString(),
      size: new Blob([fileData]).size
    };
    
    return new Promise((resolve, reject) => {
      const request = store.put(bookFile);
      request.onsuccess = () => {
        resolve(request.result);
      };
      request.onerror = () => reject(request.error);
    });
  }

  /**
   *     ( +  )
   */
  async saveBook(book) {
    
    try {
      // 1.  
      await this.saveBookMetadata(book);
      
      // 2.    
      if (book.fileData) {
        await this.saveBookFile(book.id, book.fileData);
      } else {
        console.warn('     ');
      }
      
      return true;
    } catch (error) {
      console.error(' saveBook :', error);
      throw error;
    }
  }

  /**
   *    
   */
  async getAllBooks() {
    try {
      // DB  
      if (!this.db) {
        console.warn(' DB  ,  ');
        await this.init();
      }
      
      const transaction = await this.getTransaction([this.stores.books]);
      const store = transaction.objectStore(this.stores.books);
      
      return new Promise((resolve, reject) => {
        const request = store.getAll();
        request.onsuccess = () => {
          const result = request.result || [];
          resolve(result);
        };
        request.onerror = () => {
          console.error(' getAllBooks :', request.error);
          reject(request.error);
        };
      });
    } catch (error) {
      console.error(' getAllBooks :', error);
      return []; //   
    }
  }

  /**
   *   
   */
  async getBookMetadata(bookId) {
    const transaction = await this.getTransaction([this.stores.books]);
    const store = transaction.objectStore(this.stores.books);
    
    return new Promise((resolve, reject) => {
      const request = store.get(bookId);
      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
  }

  /**
   *     ( +  )
   */
  async getBook(bookId) {
    
    try {
      // 1.  
      const metadata = await this.getBookMetadata(bookId);
      if (!metadata) {
        console.warn('  :', bookId);
        return null;
      }
      
      
      // 2.   
      try {
        const fileData = await this.getBookFile(bookId);
        if (fileData && fileData.fileData) {
          return {
            ...metadata,
            fileData: fileData.fileData
          };
        } else {
          console.warn('   :', bookId);
          return {
            ...metadata,
            fileData: null
          };
        }
      } catch (fileError) {
        console.warn('    :', fileError);
        return {
          ...metadata,
          fileData: null
        };
      }
    } catch (error) {
      console.error(' getBook :', error);
      return null;
    }
  }

  /**
   *     (  )
   */
  async getBookFile(bookId) {
    try {
      console.log('    :', bookId);
      
      await this.ensureConnection();
      const transaction = await this.getTransaction(['bookFiles'], 'readonly');
      const store = transaction.objectStore('bookFiles');
      
      return new Promise((resolve, reject) => {
        const request = store.get(bookId);
        
        request.onsuccess = () => {
          const result = request.result;
          
          if (result && result.fileData) {
            console.log('    :', {
              bookId,
              dataType: result.fileData.constructor.name,
              size: result.fileData.byteLength || result.fileData.length || 'unknown'
            });
            
            //   
            if (result.fileData instanceof ArrayBuffer) {
              if (result.fileData.byteLength > 0) {
                resolve(result.fileData);
              } else {
                console.warn('  ArrayBuffer ');
                resolve(null);
              }
            } else if (result.fileData instanceof Uint8Array) {
              if (result.fileData.length > 0) {
                resolve(result.fileData);
              } else {
                console.warn('  Uint8Array ');
                resolve(null);
              }
            } else if (typeof result.fileData === 'string') {
              //  base64  
              console.log('  base64   ...');
              try {
                const base64Data = result.fileData.replace(/^data:.*,/, '');
                const binaryString = atob(base64Data);
                const bytes = new Uint8Array(binaryString.length);
                for (let i = 0; i < binaryString.length; i++) {
                  bytes[i] = binaryString.charCodeAt(i);
                }
                console.log('    ');
                resolve(bytes);
              } catch (conversionError) {
                console.error('    :', conversionError);
                resolve(null);
              }
            } else {
              console.warn('      :', typeof result.fileData);
              resolve(null);
            }
          } else {
            console.warn('   :', bookId);
            resolve(null);
          }
        };
        
        request.onerror = () => {
          console.error('    :', request.error);
          
          //   
          setTimeout(async () => {
            try {
              console.log('    ...');
              
              //   
              const retryTransaction = await this.getTransaction(['bookFiles'], 'readonly');
              const retryStore = retryTransaction.objectStore('bookFiles');
              const retryRequest = retryStore.get(bookId);
              
              retryRequest.onsuccess = () => {
                const retryResult = retryRequest.result;
                if (retryResult && retryResult.fileData) {
                  console.log('     ');
                  resolve(retryResult.fileData);
                } else {
                  console.warn('    ');
                  resolve(null);
                }
              };
              
              retryRequest.onerror = () => {
                console.error('  ');
                resolve(null);
              };
              
            } catch (retryError) {
              console.error('   :', retryError);
              resolve(null);
            }
          }, 1000);
        };
      });
      
    } catch (error) {
      console.error(' getBookFile :', error);
      
      //  :      
      try {
        console.log('      ...');
        const fullBook = await this.getBook(bookId);
        
        if (fullBook && (fullBook.fileData || fullBook.content)) {
          console.log('      ');
          return fullBook.fileData || fullBook.content;
        } else {
          console.warn('       ');
          return null;
        }
      } catch (fallbackError) {
        console.error('    :', fallbackError);
        return null;
      }
    }
  }

  /**
   *   ( +  )
   */
  async deleteBook(bookId) {
    const transaction = await this.getTransaction([this.stores.books, this.stores.bookFiles], 'readwrite');
    
    const promises = [
      new Promise((resolve, reject) => {
        const request = transaction.objectStore(this.stores.books).delete(bookId);
        request.onsuccess = () => resolve();
        request.onerror = () => reject(request.error);
      }),
      new Promise((resolve, reject) => {
        const request = transaction.objectStore(this.stores.bookFiles).delete(bookId);
        request.onsuccess = () => resolve();
        request.onerror = () => reject(request.error);
      })
    ];
    
    await Promise.all(promises);
  }

  /**
   *   
   */
  async saveCoverImage(bookId, coverImageDataUrl) {
    try {
      if (!this.db) {
        await this.ensureConnection();
      }

      const transaction = await this.getTransaction([this.stores.books], 'readwrite');
      const store = transaction.objectStore(this.stores.books);
      
      //    
      const book = await new Promise((resolve, reject) => {
        const request = store.get(bookId);
        request.onsuccess = () => resolve(request.result);
        request.onerror = () => reject(request.error);
      });

      if (book) {
        book.coverImage = coverImageDataUrl;
        book.coverUpdatedAt = new Date().toISOString();
        
        await new Promise((resolve, reject) => {
          const request = store.put(book);
          request.onsuccess = () => resolve(request.result);
          request.onerror = () => reject(request.error);
        });
        
        console.log('    :', bookId);
        return true;
      } else {
        console.warn('    :', bookId);
        return false;
      }

    } catch (error) {
      console.error('    :', error);
      throw error;
    }
  }

  /**
   *   
   */
  async saveSettings(bookId, settings) {
    const transaction = await this.getTransaction([this.stores.settings], 'readwrite');
    const store = transaction.objectStore(this.stores.settings);
    
    const settingsData = {
      id: `reader-settings-${bookId}`,
      bookId,
      settings,
      updatedAt: new Date().toISOString()
    };
    
    return new Promise((resolve, reject) => {
      const request = store.put(settingsData);
      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
  }

  /**
   *   
   */
  async getSettings(bookId) {
    const transaction = await this.getTransaction([this.stores.settings]);
    const store = transaction.objectStore(this.stores.settings);
    
    return new Promise((resolve, reject) => {
      const request = store.get(`reader-settings-${bookId}`);
      request.onsuccess = () => resolve(request.result?.settings || null);
      request.onerror = () => reject(request.error);
    });
  }

  /**
   *   ( )
   */
  async saveBookmark(bookId, bookmark) {
    try {
      const transaction = await this.getTransaction([this.stores.settings], 'readwrite');
      const store = transaction.objectStore(this.stores.settings);
      
      const bookmarkData = {
        id: `bookmark-${bookId}`,
        bookId,
        bookmark: {
          currentPage: bookmark.currentPage,
          location: bookmark.location, // EPUB CFI location
          totalPages: bookmark.totalPages,
          chapterHref: bookmark.chapterHref, //  
          chapterTitle: bookmark.chapterTitle,
          progress: bookmark.progress, // 
          timestamp: new Date().toISOString()
        },
        updatedAt: new Date().toISOString()
      };
      
      return new Promise((resolve, reject) => {
        const request = store.put(bookmarkData);
        request.onsuccess = () => {
          console.log(`  : ${bookId} -  ${bookmark.currentPage}`);
          resolve(request.result);
        };
        request.onerror = () => reject(request.error);
      });
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }

  /**
   *   ( )
   */
  async getBookmark(bookId) {
    try {
      const transaction = await this.getTransaction([this.stores.settings]);
      const store = transaction.objectStore(this.stores.settings);
      
      return new Promise((resolve, reject) => {
        const request = store.get(`bookmark-${bookId}`);
        request.onsuccess = () => {
          const result = request.result?.bookmark || null;
          if (result) {
            console.log(`  : ${bookId} -  ${result.currentPage}`);
          }
          resolve(result);
        };
        request.onerror = () => reject(request.error);
      });
    } catch (error) {
      console.error('   :', error);
      return null;
    }
  }

  /**
   *    ()
   */
  async getAllBookmarks() {
    try {
      const transaction = await this.getTransaction([this.stores.settings]);
      const store = transaction.objectStore(this.stores.settings);
      
      return new Promise((resolve, reject) => {
        const request = store.getAll();
        request.onsuccess = () => {
          const bookmarks = request.result
            .filter(item => item.id.startsWith('bookmark-'))
            .map(item => ({
              bookId: item.bookId,
              ...item.bookmark
            }));
          resolve(bookmarks);
        };
        request.onerror = () => reject(request.error);
      });
    } catch (error) {
      console.error('    :', error);
      return [];
    }
  }

  /**
   *    (  )
   */
  async updateBookProgress(bookId, progress, lastReadPage = null) {
    try {
      const transaction = await this.getTransaction([this.stores.books], 'readwrite');
      const store = transaction.objectStore(this.stores.books);
      
      //    
      const book = await new Promise((resolve, reject) => {
        const request = store.get(bookId);
        request.onsuccess = () => resolve(request.result);
        request.onerror = () => reject(request.error);
      });

      if (book) {
        book.progress = Math.max(0, Math.min(100, progress)); // 0-100  
        book.lastOpened = new Date().toISOString();
        if (lastReadPage !== null) {
          book.lastReadPage = lastReadPage;
        }
        
        await new Promise((resolve, reject) => {
          const request = store.put(book);
          request.onsuccess = () => resolve(request.result);
          request.onerror = () => reject(request.error);
        });
        
        console.log(`  : ${bookId} - ${progress}%`);
        return true;
      } else {
        console.warn('    :', bookId);
        return false;
      }

    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }

  /**
   *    
   */
  async getStorageUsage() {
    if (!navigator.storage || !navigator.storage.estimate) {
      return { used: 0, available: 0, percentage: 0 };
    }
    
    try {
      const estimate = await navigator.storage.estimate();
      const used = estimate.usage || 0;
      const available = estimate.quota || 0;
      const percentage = available > 0 ? (used / available) * 100 : 0;
      
      return {
        used: Math.round(used / 1024 / 1024), // MB
        available: Math.round(available / 1024 / 1024), // MB
        percentage: Math.round(percentage)
      };
    } catch (error) {
      console.error('   :', error);
      return { used: 0, available: 0, percentage: 0 };
    }
  }

  /**
   *  
   */
  async cleanup() {
    const confirmCleanup = window.confirm(
      '  ?     .'
    );
    
    if (!confirmCleanup) return false;
    
    try {
      //    (30    )
      const books = await this.getAllBooks();
      const thirtyDaysAgo = new Date(Date.now() - 30 * 24 * 60 * 60 * 1000);
      
      const oldBooks = books.filter(book => {
        const lastOpened = book.lastOpened ? new Date(book.lastOpened) : new Date(book.addedDate);
        return lastOpened < thirtyDaysAgo;
      });
      
      for (const book of oldBooks) {
        await this.deleteBook(book.id);
      }
      
      return true;
    } catch (error) {
      console.error('  :', error);
      return false;
    }
  }

  /**
   * localStorage IndexedDB  
   */
  async migrateFromLocalStorage() {
    try {
      
      //    (  )
      const migrationFlag = localStorage.getItem('ereader-migration-done');
      if (migrationFlag) {
        return true;
      }
      
      //   
      const booksData = localStorage.getItem('ereader-books');
      if (booksData) {
        const books = JSON.parse(booksData);
        
        for (let i = 0; i < books.length; i++) {
          const book = books[i];
          
          try {
            //  
            await this.saveBookMetadata(book);
            
            //     
            if (book.fileData || book.content) {
              await this.saveBookFile(book.id, book.fileData || book.content);
            }
          } catch (bookError) {
            console.error(`   : ${book.title}`, bookError);
            //      
          }
        }
        
      }
      
      //    
      localStorage.setItem('ereader-migration-done', 'true');
      
      //    localStorage  ()
      const keysToRemove = ['ereader-books']; //   
      keysToRemove.forEach(key => {
        if (localStorage.getItem(key)) {
          localStorage.removeItem(key);
        }
      });
      
      return true;
    } catch (error) {
      console.error('  :', error);
      return false;
    }
  }
}

//   (  )
const indexedDBStorage = new IndexedDBStorage();

export { IndexedDBStorage };
export default indexedDBStorage; 