import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import indexedDBStorage from '../utils/IndexedDBStorage';
import { CoverExtractor } from '../utils/CoverExtractor';
import { EpubMetadataExtractor } from '../utils/EpubMetadataExtractor';

const BookContext = createContext();

export const useBooks = () => {
  const context = useContext(BookContext);
  if (!context) {
    throw new Error('useBooks must be used within a BookProvider');
  }
  return context;
};

export const BookProvider = ({ children }) => {
  const [books, setBooks] = useState([]);
  const [currentBook, setCurrentBook] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [storageUsage, setStorageUsage] = useState({ used: 0, available: 0, percentage: 0 });

  //      useCallback 
  const updateStorageUsage = useCallback(async () => {
    try {
      const usage = await indexedDBStorage.getStorageUsage();
      setStorageUsage(usage);
    } catch (error) {
      console.error('    :', error);
    }
  }, []);

  // IndexedDB     (  )
  useEffect(() => {
    let isMounted = true; // cleanup  

    const initializeStorage = async () => {
      try {
        console.log('   -   ');
        setIsLoading(true);
        
        // 1:  localStorage   ( )
        console.log(' 1: localStorage  ');
        await loadFromLocalStorage();
        
        if (isMounted) {
          console.log(' 1  -   ');
          //   
          setIsLoading(false);
          console.log('    ');
        }
        
        // 2:  IndexedDB   
        setTimeout(async () => {
          if (!isMounted) return;
          
          try {
            console.log(' 2:  IndexedDB ');
            
            // 2  IndexedDB  
            await indexedDBStorage.init(2000);
            console.log(' IndexedDB  ');
            
            // IndexedDB  
            const indexedBooks = await indexedDBStorage.getAllBooks();
            if (indexedBooks && indexedBooks.length > 0 && isMounted) {
              console.log(` IndexedDB ${indexedBooks.length}  `);
              setBooks(indexedBooks);
              await updateStorageUsage();
            }
            
            //  
            await checkMigration();
            
          } catch (indexedError) {
            console.warn(' IndexedDB  :', indexedError.message);
            console.log(' localStorage   ');
          }
        }, 100); // 100ms   
        
      } catch (error) {
        console.error('  :', error);
        if (isMounted) {
          setBooks([]);
          setIsLoading(false);
        }
      }
    };

    // localStorage  
    const loadFromLocalStorage = async () => {
      try {
        const savedBooks = localStorage.getItem('ereader-books');
        console.log(' localStorage  :', savedBooks ? 'exists' : 'null');
        
        if (savedBooks) {
          const parsedBooks = JSON.parse(savedBooks);
          console.log('   :', parsedBooks.length);
          
          //    
          const cleanedBooks = parsedBooks.map(book => {
            const { fileData, content, ...metadata } = book;
            return {
              ...metadata,
              hasFileData: !!(fileData || content),
              fileSize: book.size || 0,
              isFromLocalStorage: true
            };
          });
          
          console.log('   :', cleanedBooks.length);
          console.log('   :', cleanedBooks[0]?.title);
          
          setBooks(cleanedBooks);
          console.log(' setBooks  ');
          
          //     
          setTimeout(() => {
            console.log('    ');
          }, 100);
          
        } else {
          console.log('   ');
          setBooks([]);
        }
      } catch (error) {
        console.error(' localStorage  :', error);
        setBooks([]);
      }
    };

    //   ()
    const checkMigration = async () => {
      try {
        const hasLocalStorageData = localStorage.getItem('ereader-books');
        const migrationDone = localStorage.getItem('ereader-migration-done');
        
        if (hasLocalStorageData && !migrationDone) {
          console.log('   ');
          
          setTimeout(async () => {
            try {
              const success = await indexedDBStorage.migrateFromLocalStorage();
              if (success && isMounted) {
                console.log('  ');
                const newBooks = await indexedDBStorage.getAllBooks();
                if (newBooks && newBooks.length > 0) {
                  setBooks(newBooks);
                  await updateStorageUsage();
                }
              }
            } catch (migrationError) {
              console.error('  :', migrationError);
            }
          }, 1000);
        }
      } catch (error) {
        console.error('   :', error);
      }
    };

    initializeStorage();

    // cleanup 
    return () => {
      isMounted = false;
    };
  }, [updateStorageUsage]); // ESLint  

  const addBook = useCallback(async (book) => {
    try {
      const newBook = {
        id: Date.now().toString(),
        ...book,
        addedDate: new Date().toISOString(),
        lastOpened: null,
        progress: 0
      };
      
      //     
      if (book.size && book.size > 50 * 1024 * 1024) { // 50MB 
        const confirm = window.confirm(
          `  ${(book.size / 1024 / 1024).toFixed(1)}MB.  ?`
        );
        if (!confirm) return null;
      }
      
      console.log('   :', newBook.title);
      
      //  React   ( UI )
      setBooks(prev => [...prev, newBook]);
      console.log(' React   ');
      
      //  IndexedDB 
      try {
        console.log(' IndexedDB  ...');
        
        //    
        const fileData = book.fileData || book.content;
        if (!fileData) {
          console.warn('   :', newBook.title);
        } else {
          console.log('   :', fileData.length, '');
        }
        
        //     ( +  )
        const completeBook = {
          ...newBook,
          fileData: fileData,
          hasFileData: !!fileData
        };
        
        // IndexedDB    
        await indexedDBStorage.saveBook(completeBook);
        console.log(' IndexedDB   ( +  )');
        
        //  EPUB   (EPUB  )
        if (book.type === 'epub') {
          setTimeout(async () => {
            try {
              console.log('    :', newBook.title);
              const coverImage = await CoverExtractor.extractCover(fileData);
              
              if (coverImage) {
                // IndexedDB   
                await indexedDBStorage.saveCoverImage(newBook.id, coverImage);
                
                // React  
                setBooks(prev => prev.map(b => 
                  b.id === newBook.id 
                    ? { ...b, coverImage } 
                    : b
                ));
                
                console.log('      ');
              } else {
                console.log('     ');
              }
            } catch (coverError) {
              console.warn('    ():', coverError.message);
            }
          }, 1000); // 1   
        }
        
        //    
        await updateStorageUsage();
        
      } catch (storageError) {
        console.error(' IndexedDB  :', storageError);
        
        //   - UI  
        setBooks(prev => prev.filter(b => b.id !== newBook.id));
        
        if (storageError.name === 'QuotaExceededError') {
          alert(`  . 
 : ${storageUsage.used}MB / ${storageUsage.available}MB
     .`);
        } else {
          alert(`    : ${storageError.message}`);
        }
        
        return null;
      }
      
      console.log(`   : ${newBook.title} (${(book.size / 1024 / 1024).toFixed(2)} MB)`);
      return newBook;
      
    } catch (error) {
      console.error('   :', error);
      
      if (error.name === 'QuotaExceededError') {
        alert(`  . 
 : ${storageUsage.used}MB / ${storageUsage.available}MB
     .`);
      } else {
        alert('    .');
      }
      
      return null;
    }
  }, [updateStorageUsage, storageUsage.used, storageUsage.available]);

  const removeBook = useCallback(async (bookId) => {
    try {
      await indexedDBStorage.deleteBook(bookId);
      setBooks(prev => prev.filter(book => book.id !== bookId));
      await updateStorageUsage();
      console.log(`  : ${bookId}`);
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    }
  }, [updateStorageUsage]);

  const updateBook = useCallback(async (bookId, updates) => {
    try {
      const updatedBook = books.find(book => book.id === bookId);
      if (!updatedBook) return;
      
      const newBookData = { ...updatedBook, ...updates };
      await indexedDBStorage.saveBookMetadata(newBookData);
      
      setBooks(prev => prev.map(book => 
        book.id === bookId ? newBookData : book
      ));
    } catch (error) {
      console.error('  :', error);
    }
  }, [books]);

  const getBookById = useCallback((bookId) => {
    return books.find(book => book.id === bookId);
  }, [books]);

  // IndexedDB      
  const getBookWithFile = useCallback(async (bookId) => {
    console.log(' getBookWithFile :', bookId);
    
    try {
      // 1.  
      const book = books.find(book => book.id === bookId);
      if (!book) {
        console.error('      ID:', bookId);
        return null;
      }
      
      console.log('  :', book.title);
      
      // 2. IndexedDB   
      try {
        console.log(' IndexedDB    ...');
        const bookWithFileData = await indexedDBStorage.getBook(bookId);
        
        if (bookWithFileData && bookWithFileData.fileData) {
          console.log(' IndexedDB   ');
          console.log('  :', bookWithFileData.fileData.length, '');
          
          return {
            ...book,
            fileData: bookWithFileData.fileData
          };
        } else {
          console.warn(' IndexedDB   ');
          return {
            ...book,
            fileData: null
          };
        }
      } catch (dbError) {
        console.error(' IndexedDB :', dbError);
        return {
          ...book,
          fileData: null
        };
      }
    } catch (error) {
      console.error(' getBookWithFile  :', error);
      return null;
    }
  }, [books]);



  //   
  const saveBookmark = useCallback(async (bookId, bookmarkData) => {
    try {
      await indexedDBStorage.saveBookmark(bookId, bookmarkData);
      console.log(`   : ${bookId}`);
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }, []);

  //   
  const getBookmark = useCallback(async (bookId) => {
    try {
      const bookmark = await indexedDBStorage.getBookmark(bookId);
      return bookmark;
    } catch (error) {
      console.error('   :', error);
      return null;
    }
  }, []);

  //    
  const getAllBookmarks = useCallback(async () => {
    try {
      return await indexedDBStorage.getAllBookmarks();
    } catch (error) {
      console.error('    :', error);
      return [];
    }
  }, []);

  const getMostPopular = useCallback(() => {
    return books
      .filter(book => book.lastOpened)
      .sort((a, b) => new Date(b.lastOpened) - new Date(a.lastOpened))
      .slice(0, 6);
  }, [books]);

  const getRecentlyAdded = useCallback(() => {
    return books
      .sort((a, b) => new Date(b.addedDate) - new Date(a.addedDate))
      .slice(0, 6);
  }, [books]);

  //   
  const cleanupStorage = useCallback(async () => {
    try {
      const success = await indexedDBStorage.cleanup();
      if (success) {
        //   
        const updatedBooks = await indexedDBStorage.getAllBooks();
        setBooks(updatedBooks || []);
        await updateStorageUsage();
        
        alert('   .');
      }
    } catch (error) {
      console.error('   :', error);
      alert('     .');
    }
  }, [updateStorageUsage]);

  //   
  const saveReadingSettings = useCallback(async (bookId, settings) => {
    try {
      await indexedDBStorage.saveSettings(bookId, settings);
    } catch (error) {
      console.error('   :', error);
    }
  }, []);

  const getReadingSettings = useCallback(async (bookId) => {
    try {
      return await indexedDBStorage.getSettings(bookId);
    } catch (error) {
      console.error('   :', error);
      return null;
    }
  }, []);

  //      
  const extractCoverForExistingBooks = async () => {
    try {
      console.log('     ...');
      
      // EPUB     
      const booksNeedingCovers = books.filter(book => 
        book.type === 'epub' && !book.coverImage
      );
      
      if (booksNeedingCovers.length === 0) {
        console.log('     .');
        return;
      }
      
      console.log(` ${booksNeedingCovers.length}    ...`);
      
      let successCount = 0;
      let failCount = 0;
      
      for (const book of booksNeedingCovers) {
        try {
          console.log(` "${book.title}"   ...`);
          
          // IndexedDB   
          const bookWithFile = await indexedDBStorage.getBook(book.id);
          
          if (!bookWithFile || !bookWithFile.fileData) {
            console.warn(` "${book.title}"     `);
            failCount++;
            continue;
          }
          
          //  
          const coverImage = await CoverExtractor.extractCover(bookWithFile.fileData);
          
          if (coverImage) {
            // IndexedDB   
            await indexedDBStorage.saveCoverImage(book.id, coverImage);
            
            // React  
            setBooks(prev => prev.map(b => 
              b.id === book.id 
                ? { ...b, coverImage }
                : b
            ));
            
            console.log(` "${book.title}"   `);
            successCount++;
          } else {
            console.warn(` "${book.title}"    `);
            failCount++;
          }
          
          //       (UI  )
          await new Promise(resolve => setTimeout(resolve, 100));
          
        } catch (error) {
          console.error(` "${book.title}"   :`, error);
          failCount++;
        }
      }
      
      console.log(`   :  ${successCount},  ${failCount}`);
      
      if (successCount > 0) {
        //    
        await updateStorageUsage();
      }
      
    } catch (error) {
      console.error('      :', error);
    }
  };



  //   EPUB    
  const updateMetadataForExistingBooks = async () => {
    try {
      console.log('     ...');
      
      // EPUB     
      const booksNeedingMetadata = books.filter(book => 
        book.type === 'epub' && (
          book.author === '  ' || 
          !book.publisher || 
          !book.language ||
          !book.description
        )
      );
      
      if (booksNeedingMetadata.length === 0) {
        console.log('     .');
        return;
      }
      
      console.log(` ${booksNeedingMetadata.length}    ...`);
      
      let successCount = 0;
      let failCount = 0;
      
      for (const book of booksNeedingMetadata) {
        try {
          console.log(` "${book.title}"   ...`);
          
          // IndexedDB   
          const bookWithFile = await indexedDBStorage.getBook(book.id);
          
          if (!bookWithFile || !bookWithFile.fileData) {
            console.warn(` "${book.title}"     `);
            failCount++;
            continue;
          }
          
          //  
          const metadata = await EpubMetadataExtractor.extractMetadata(bookWithFile.fileData);
          
          //   
          const updates = {};
          let hasUpdates = false;
          
          if (metadata.title && metadata.title !== book.title) {
            updates.title = metadata.title;
            hasUpdates = true;
          }
          
          if (metadata.author && metadata.author !== book.author) {
            updates.author = metadata.author;
            hasUpdates = true;
          }
          
          if (metadata.publisher && !book.publisher) {
            updates.publisher = metadata.publisher;
            hasUpdates = true;
          }
          
          if (metadata.language && !book.language) {
            updates.language = metadata.language;
            hasUpdates = true;
          }
          
          if (metadata.description && !book.description) {
            updates.description = metadata.description;
            hasUpdates = true;
          }
          
          if (hasUpdates) {
            const updatedBook = { ...book, ...updates };
            
            // IndexedDB   
            await indexedDBStorage.saveBookMetadata(updatedBook);
            
            // React  
            setBooks(prev => prev.map(b => 
              b.id === book.id ? updatedBook : b
            ));
            
            console.log(` "${book.title}"   :`, updates);
            successCount++;
          } else {
            console.log(` "${book.title}"   `);
          }
          
          //       (UI  )
          await new Promise(resolve => setTimeout(resolve, 200));
          
        } catch (error) {
          console.error(` "${book.title}"   :`, error);
          failCount++;
        }
      }
      
      console.log(`   :  ${successCount},  ${failCount}`);
      
    } catch (error) {
      console.error('      :', error);
    }
  };

  const value = {
    books,
    currentBook,
    setCurrentBook,
    isLoading,
    storageUsage,
    addBook,
    removeBook,
    updateBook,
    getBookById,
    getBookWithFile,
    saveBookmark,
    getBookmark,
    getAllBookmarks,
    getMostPopular,
    getRecentlyAdded,
    cleanupStorage,
    saveReadingSettings,
    getReadingSettings,
    updateStorageUsage,
    extractCoverForExistingBooks,
    updateMetadataForExistingBooks
  };

  return (
    <BookContext.Provider value={value}>
      {children}
    </BookContext.Provider>
  );
}; 