/**
 *   
 *            
 */

class EmergencyFix {
  /**
   *     ( )
   */
  static clearAllData() {
    const confirmClear = window.confirm(
      ' :    .\n\n' +
      '-   \n' +
      '-  \n' +
      '- AI  \n' +
      '- \n' +
      '- \n\n' +
      ' ?'
    );
    
    if (!confirmClear) return false;
    
    try {
      // localStorage  
      const keys = Object.keys(localStorage);
      keys.forEach(key => {
        if (key.startsWith('ereader-') || key.startsWith('reader-')) {
          localStorage.removeItem(key);
        }
      });
      
      // IndexedDB 
      const deleteDB = indexedDB.deleteDatabase('EReaderDB');
      deleteDB.onsuccess = () => {
        console.log(' IndexedDB  ');
        alert('  .  .');
        window.location.reload();
      };
      
      deleteDB.onerror = () => {
        console.error(' IndexedDB  ');
        alert('     .  .');
        window.location.reload();
      };
      
      return true;
    } catch (error) {
      console.error('  :', error);
      alert('    .');
      return false;
    }
  }
  
  /**
   *   
   */
  static resetMigration() {
    try {
      localStorage.removeItem('ereader-migration-done');
      console.log('    ');
      alert('  .  .');
      window.location.reload();
      return true;
    } catch (error) {
      console.error('   :', error);
      return false;
    }
  }
  
  /**
   * IndexedDB  (localStorage )
   */
  static resetIndexedDB() {
    const confirm = window.confirm(
      'IndexedDB ?\n\n' +
      '       ,\n' +
      ' localStorage  .'
    );
    
    if (!confirm) return false;
    
    try {
      const deleteDB = indexedDB.deleteDatabase('EReaderDB');
      deleteDB.onsuccess = () => {
        localStorage.removeItem('ereader-migration-done');
        console.log(' IndexedDB  ');
        alert('IndexedDB .  .');
        window.location.reload();
      };
      
      deleteDB.onerror = () => {
        console.error(' IndexedDB  ');
        alert('IndexedDB  .');
      };
      
      return true;
    } catch (error) {
      console.error('IndexedDB  :', error);
      return false;
    }
  }
  
  /**
   *   
   */
  static async diagnose() {
    const report = [];
    
    // localStorage 
    const localStorageKeys = Object.keys(localStorage).filter(key => 
      key.startsWith('ereader-') || key.startsWith('reader-')
    );
    report.push(` localStorage : ${localStorageKeys.length}`);
    localStorageKeys.forEach(key => {
      const value = localStorage.getItem(key);
      const size = new Blob([value]).size;
      report.push(`  - ${key}: ${(size / 1024).toFixed(1)} KB`);
    });
    
    // IndexedDB 
    try {
      const databases = await indexedDB.databases();
      const eReaderDB = databases.find(db => db.name === 'EReaderDB');
      report.push(` IndexedDB: ${eReaderDB ? '' : ''}`);
    } catch (error) {
      report.push(` IndexedDB:   (${error.message})`);
    }
    
    //   
    report.push(` IndexedDB : ${!!window.indexedDB}`);
    report.push(` Storage API : ${!!navigator.storage}`);
    
    //  
    const migrationDone = localStorage.getItem('ereader-migration-done');
    report.push(`  : ${migrationDone ? '' : ''}`);
    
    const reportText = report.join('\n');
    console.log('   :\n' + reportText);
    alert(' :\n\n' + reportText + '\n\n      .');
    
    return report;
  }
  
  /**
   *  window     
   */
  static registerGlobalFunctions() {
    window.emergencyFix = {
      clearAll: this.clearAllData,
      resetMigration: this.resetMigration,
      resetIndexedDB: this.resetIndexedDB,
      diagnose: this.diagnose,
      help: () => {
        const helpText = `
 E-Reader   

      :

 emergencyFix.diagnose()
  -   

 emergencyFix.resetMigration()
  -   

 emergencyFix.resetIndexedDB()
  - IndexedDB  (localStorage )

 emergencyFix.clearAll()
  -    ( )

 emergencyFix.help()
  -   

:   (F12) > Console   
        `;
        console.log(helpText);
        alert('     .\n\nF12 > Console  .');
      }
    };
    
    console.log('    .');
    console.log(': emergencyFix.help()');
  }
}

export default EmergencyFix; 