import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { FiX, FiChevronRight, FiChevronDown } from 'react-icons/fi';

const Container = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  background: white;
`;

const Header = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #e0e0e0;
`;

const Title = styled.h3`
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin: 0;
`;

const CloseButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  color: #666;
  padding: 4px;
  border-radius: 4px;
  
  &:hover {
    background: #f0f0f0;
  }
`;

const Content = styled.div`
  flex: 1;
  overflow-y: auto;
  padding: 0;
`;

const ChapterList = styled.div`
  padding: 0;
`;

const ChapterItem = styled.div`
  cursor: pointer;
  transition: background-color 0.2s ease;
  
  &:hover {
    background: #f8f9fa;
  }
  
  ${props => props.active && `
    background: #e3f2fd;
    
    &:hover {
      background: #e3f2fd;
    }
  `}
`;

const ChapterHeader = styled.div`
  display: flex;
  align-items: center;
  padding: 8px 20px;
  padding-left: ${props => 20 + (props.level * 20)}px;
  gap: 8px;
  min-height: 40px;
`;

const ExpandButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  color: #666;
  padding: 2px;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  
  &:hover {
    background: #e0e0e0;
  }
  
  ${props => !props.hasChildren && `
    visibility: hidden;
  `}
`;

const ChapterTitle = styled.div`
  flex: 1;
  font-size: ${props => props.level === 0 ? '0.95rem' : '0.85rem'};
  color: ${props => props.active ? '#1976d2' : '#333'};
  font-weight: ${props => {
    if (props.active) return '600';
    if (props.level === 0) return '500';
    return '400';
  }};
  line-height: 1.4;
  text-decoration: none;
  cursor: pointer;
  transition: color 0.2s ease;
  opacity: ${props => props.level > 0 ? '0.9' : '1'};
  
  &:hover {
    color: #1976d2;
  }
`;

const TableOfContents = ({ book, currentPage, onPageChange, onClose, chapters: bookChapters, totalPages }) => {
  const [expandedChapters, setExpandedChapters] = useState(new Set());

  //    
  useEffect(() => {
    console.log(' TableOfContents  :', bookChapters);
    if (bookChapters && bookChapters.length > 0) {
      bookChapters.forEach((chapter, index) => {
        console.log(`  ${index}:`, {
          title: chapter.title,
          href: chapter.href,
          subitemsCount: chapter.subitems ? chapter.subitems.length : 0,
          subitems: chapter.subitems
        });
      });
    }
  }, [bookChapters]);

  const flattenChapters = (chapters, level = 0) => {
    if (!chapters || !Array.isArray(chapters)) {
      console.warn(' flattenChapters:    :', chapters);
      return [];
    }
    
    const flattened = [];
    
    chapters.forEach((chapter, index) => {
      const chapterId = chapter.id || `chapter-${level}-${index}`;
      
      //   
      flattened.push({
        ...chapter,
        id: chapterId,
        level,
        hasSubitems: chapter.subitems && Array.isArray(chapter.subitems) && chapter.subitems.length > 0
      });
      
      //       
      if (chapter.subitems && 
          Array.isArray(chapter.subitems) && 
          chapter.subitems.length > 0 && 
          expandedChapters.has(chapterId)) {
        
        console.log(`  ${chapterId}  :`, chapter.subitems);
        const subChapters = flattenChapters(chapter.subitems, level + 1);
        flattened.push(...subChapters);
      }
    });
    
    console.log(` Level ${level}  :`, flattened);
    return flattened;
  };

  // bookChapters    
  const chapters = React.useMemo(() => {
    if (bookChapters && bookChapters.length > 0) {
      // EPUB    
      return flattenChapters(bookChapters.map((chapter, index) => ({
        id: `chapter-${index}`,
        title: chapter.title,
        href: chapter.href,
        page: 1,
        chapterIndex: index,
        subitems: chapter.subitems || []
      })));
    } else {
      // :  
      if (book?.type === 'pdf') {
        return [
          { id: 'cover', title: '', page: 1, level: 0, hasSubitems: false },
          { id: 'toc', title: '', page: 2, level: 0, hasSubitems: false },
          { id: 'ch1', title: '1 - ', page: 5, level: 0, hasSubitems: false }
        ];
      } else {
        return [
          { id: 'loading', title: '  ...', page: 1, level: 0, hasSubitems: false }
        ];
      }
    }
  }, [bookChapters, book?.id, book?.type, expandedChapters]);

  // / 
  const toggleExpanded = (chapterId, e) => {
    e.stopPropagation();
    setExpandedChapters(prev => {
      const newSet = new Set(prev);
      if (newSet.has(chapterId)) {
        newSet.delete(chapterId);
      } else {
        newSet.add(chapterId);
      }
      return newSet;
    });
  };

  const handleChapterClick = (chapter) => {
    // href  ReactReader  ,   
    if (chapter.href && onPageChange) {
      // ReactReader  href  
      onPageChange({ type: 'chapter', href: chapter.href, chapterIndex: chapter.chapterIndex });
    } else if (chapter.chapterIndex !== undefined && onPageChange) {
      // SimpleEpubRenderer     
      onPageChange({ type: 'chapter', chapterIndex: chapter.chapterIndex });
    } else if (chapter.page) {
      onPageChange(chapter.page);
    }
  };

  const getCurrentChapter = () => {
    //     
    for (let i = chapters.length - 1; i >= 0; i--) {
      if (chapters[i].page <= currentPage) {
        return chapters[i].id;
      }
    }
    return null;
  };

  const currentChapterId = getCurrentChapter();

  return (
    <Container>
      <Header>
        <Title></Title>
        <CloseButton onClick={onClose}>
          <FiX size={18} />
        </CloseButton>
      </Header>

      <Content>
        <ChapterList>
          {chapters.length === 0 ? (
            <ChapterItem>
              <ChapterHeader level={0}>
                <ChapterTitle level={0} style={{ color: '#999', fontStyle: 'italic' }}>
                    ...
                </ChapterTitle>
              </ChapterHeader>
            </ChapterItem>
          ) : (
            chapters.map(chapter => {
              const isActive = chapter.id === currentChapterId;
              const isDisabled = chapter.title === '  ...';
              const isExpanded = expandedChapters.has(chapter.id);

              return (
                <ChapterItem key={chapter.id} active={isActive}>
                  <ChapterHeader level={chapter.level}>
                    <ExpandButton 
                      hasChildren={chapter.hasSubitems}
                      onClick={(e) => toggleExpanded(chapter.id, e)}
                    >
                      {chapter.hasSubitems && (
                        isExpanded ? <FiChevronDown size={12} /> : <FiChevronRight size={12} />
                      )}
                    </ExpandButton>
                    
                    <ChapterTitle 
                      level={chapter.level}
                      active={isActive}
                      onClick={() => !isDisabled && handleChapterClick(chapter)}
                      style={{ 
                        color: isDisabled ? '#999' : (isActive ? '#1976d2' : '#333'),
                        fontStyle: isDisabled ? 'italic' : 'normal',
                        cursor: isDisabled ? 'default' : 'pointer'
                      }}
                    >
                      {chapter.title}
                    </ChapterTitle>
                  </ChapterHeader>
                </ChapterItem>
              );
            })
          )}
        </ChapterList>
      </Content>
    </Container>
  );
};

export default TableOfContents; 