import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { FiArrowLeft, FiSettings, FiMessageSquare, FiBookmark } from 'react-icons/fi';
import { useBooks } from '../../context/BookContext';
import TableOfContents from './TableOfContents';
import ReadingSettings from './ReadingSettings';
import TextRenderer from './TextRenderer';

import ReactReaderRenderer from './ReactReaderRenderer';
import AIChat from './AIChat';
import LoadingSpinner from './LoadingSpinner';

const Container = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  background: #fafafa;
`;

const Sidebar = styled.div`
  width: ${props => props.isOpen ? '450px' : '0'};
  background: white;
  border-right: 1px solid #e0e0e0;
  transition: width 0.3s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;
`;

const MainContent = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
`;

const Header = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  background: white;
  border-bottom: 1px solid #e0e0e0;
`;

const HeaderLeft = styled.div`
  display: flex;
  align-items: center;
  gap: 15px;
`;

const BackButton = styled.button`
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  border-radius: 8px;
  color: #666;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    background: #f0f0f0;
  }
`;

const BookTitle = styled.h1`
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
  margin: 0;
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

const HeaderRight = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`;

const HeaderButton = styled.button`
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  border-radius: 8px;
  color: #666;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  
  &:hover {
    background: #f0f0f0;
  }
  
  ${props => props.active && `
    background: #e3f2fd;
    color: #1976d2;
  `}
`;

const ReadingArea = styled.div`
  flex: 1;
  display: flex;
  overflow: hidden;
`;

const ContentArea = styled.div`
  flex: 1;
  position: relative;
  overflow: hidden;
`;

const ChatSidebar = styled.div`
  width: ${props => props.isOpen ? props.width + 'px' : '0'};
  min-width: ${props => props.isOpen ? '400px' : '0'};
  max-width: ${props => props.isOpen ? '800px' : '0'};
  background: white;
  border-left: 1px solid #e0e0e0;
  transition: ${props => props.isResizing ? 'none' : 'width 0.3s ease'};
  overflow: hidden;
  position: relative;
`;

const ResizeHandle = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background: transparent;
  cursor: col-resize;
  z-index: 10;
  
  &:hover {
    background: #4CAF50;
  }
  
  &:active {
    background: #4CAF50;
  }
`;

// LoadingContainer    (LoadingSpinner  )

const ErrorContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #f44336;
  text-align: center;
  padding: 20px;
`;

const ReaderView = () => {
  const { bookId } = useParams();
  const navigate = useNavigate();
  const { getBookById, getBookWithFile, saveBookmark, getBookmark, saveReadingSettings, getReadingSettings } = useBooks();
  
  const [book, setBook] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // UI 
  const [showTOC, setShowTOC] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [showChat, setShowChat] = useState(false);
  const [chatSidebarWidth, setChatSidebarWidth] = useState(600);
  const [isResizing, setIsResizing] = useState(false);
  
  // ReactReaderRenderer ref
  const reactReaderRef = useRef(null);
  
  //  
  const [readingSettings, setReadingSettings] = useState({
    fontSize: 16,
    fontFamily: 'serif',
    lineHeight: 1.6,
    theme: 'light',
    margin: 20,
    textAlign: 'left'
  });
  
  //   
  const [lastLocation, setLastLocation] = useState(null); // EPUB  
  const [pendingBookmarkLocation, setPendingBookmarkLocation] = useState(null); //    
  
  //     
  const [isRefreshRecovery, setIsRefreshRecovery] = useState(false);




  


  //   
  useEffect(() => {
    const handleBeforeUnload = () => {
      //   
      sessionStorage.setItem('epub-reader-refresh', 'true');
    };

    const handleLoad = () => {
      //     
      const wasRefresh = sessionStorage.getItem('epub-reader-refresh');
      if (wasRefresh) {
        setIsRefreshRecovery(true);
        sessionStorage.removeItem('epub-reader-refresh');
        console.log('   ,    ');
      }
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    window.addEventListener('load', handleLoad);
    
    //    
    handleLoad();

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
      window.removeEventListener('load', handleLoad);
    };
  }, []);

  useEffect(() => {
    let isMounted = true; // cleanup  

    const loadBook = async () => {
      try {
        setLoading(true);
        
        // 1:   ( )
        let basicBook = getBookById(bookId);
        
        //   BookContext      IndexedDB 
        if (!basicBook) {
          console.log(' BookContext    , IndexedDB   ...');
          
          try {
            // IndexedDB    
            const indexedDBStorage = (await import('../../utils/IndexedDBStorage')).default;
            await indexedDBStorage.init(3000); // 3 
            
            const bookFromDB = await indexedDBStorage.getBookMetadata(bookId);
            if (bookFromDB) {
              console.log(' IndexedDB    :', bookFromDB.title);
              basicBook = bookFromDB;
            } else {
              console.error(' IndexedDB    :', bookId);
              if (isMounted) {
                setError('   .    .');
              }
              return;
            }
          } catch (dbError) {
            console.error(' IndexedDB   :', dbError);
            if (isMounted) {
              setError('    .  .');
            }
            return;
          }
        }
        
        
        // 2: IndexedDB     ( )
        let bookWithFile = { ...basicBook };
        
        try {
          // BookContext getBookWithFile   
          let bookWithFileData = null;
          
          if (getBookWithFile) {
            try {
              bookWithFileData = await getBookWithFile(bookId);
            } catch (contextError) {
              console.warn(' BookContext   :', contextError);
            }
          }
          
          // BookContext     IndexedDB 
          if (!bookWithFileData || (!bookWithFileData.fileData && !bookWithFileData.content)) {
            console.log('  IndexedDB    ...');
            
            const indexedDBStorage = (await import('../../utils/IndexedDBStorage')).default;
            const fileData = await indexedDBStorage.getBookFile(bookId);
            
            if (fileData) {
              console.log(' IndexedDB    ');
              bookWithFileData = { fileData };
            }
          }
          
          if (bookWithFileData && (bookWithFileData.fileData || bookWithFileData.content)) {
            console.log('    ');
            bookWithFile = {
              ...basicBook,
              fileData: bookWithFileData.fileData || bookWithFileData.content
            };
          } else {
            console.warn('     ');
            bookWithFile.content = `
              <div style="padding: 40px; text-align: center;">
                <h1>${basicBook.title}</h1>
                <p>: ${basicBook.author}</p>
                <p style="color: #f44336; margin: 20px 0;">    .</p>
                <p>     .</p>
                <p>    .</p>
                <p style="margin-top: 30px; color: #666; font-size: 0.9em;">
                   : ${(basicBook.size / 1024 / 1024).toFixed(2)} MB<br/>
                   : ${basicBook.type?.toUpperCase() || '  '}
                </p>
              </div>
            `;
          }
        } catch (dbError) {
          console.error('    :', dbError);
          bookWithFile.content = `
            <div style="padding: 40px; text-align: center;">
              <h1>${basicBook.title}</h1>
              <p style="color: #f44336;">  .</p>
              <p>: ${dbError.message}</p>
              <p>    .</p>
            </div>
          `;
        }
        
        
        if (isMounted) {
          setBook(bookWithFile);
          setLoading(false);
        }
        
        //    
        const savedSettings = await getReadingSettings(bookId);
        if (savedSettings && isMounted) {
          setReadingSettings(savedSettings);
        }
        
        //   (  )
        const savedBookmark = await getBookmark(bookId);
        if (savedBookmark && isMounted) {
          console.log('   :', savedBookmark);
          
          // EPUB  location   (  )
          if (savedBookmark.location && bookWithFile.type === 'epub') {
            //    ,    
            setLastLocation(savedBookmark.location);
            console.log(' EPUB   :', savedBookmark.location);
            
            //      (ReactReaderRenderer )
            setPendingBookmarkLocation(savedBookmark.location);
          }
        }
        
      } catch (err) {
        if (isMounted) {
          setError('    .');
          console.error('Book loading error:', err);
        }
      } finally {
        if (isMounted) {
          setLoading(false);
        }
      }
    };

    if (bookId) {
      loadBook();
    }

    return () => {
      isMounted = false;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bookId]); // getBookById, getBookWithFile, getReadingSettings   

  //     
  useEffect(() => {
    if (book && bookId) {
      saveReadingSettings(bookId, readingSettings);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [readingSettings, bookId, book]); // saveReadingSettings   

  //     
  useEffect(() => {
    //    
    if (book && 
        book.id && 
        bookId &&
        book.id === bookId) { // bookId book.id  
      
      //    (  )
      const bookmarkData = {
        location: typeof lastLocation === 'string' ? lastLocation : null, //  
        timestamp: new Date().toISOString()
      };

      //       
      const timeoutId = setTimeout(async () => {
        try {
          await saveBookmark(bookId, bookmarkData);
          console.log(`   :  ${bookmarkData.location}`);
        } catch (error) {
          console.warn('   :', error);
        }
      }, 2000); // 2    

      return () => clearTimeout(timeoutId);
    } else {
      console.log('    :', {
        book: !!book,
        bookId: bookId,
        bookIdMatch: book?.id === bookId,
        lastLocation: lastLocation
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bookId, book, lastLocation]); // saveBookmark   

  const handleBack = () => {
    navigate('/');
  };

  const handlePageChange = useCallback((page) => {
    console.log(' ReaderView handlePageChange :', typeof page, page);
    
    // EPUB    ( )
    if (typeof page === 'object' && page !== null) {
      if (page.type === 'location_changed' || page.type === 'relocated') {
        // EPUB    ()
        if (page.location) {
          setLastLocation(page.location);
          console.log('   :', page.location);
          
          //  
          if (book?.id) {
            saveBookmark(book.id, page.location);
          }
        }
        return;
      }
      
      //   
      if (page.type === 'chapter') {
        console.log('  :', page);
        
        if (reactReaderRef.current) {
          const chapterHref = page.href;
          if (chapterHref) {
            reactReaderRef.current.goToChapter(chapterHref);
          } else {
            console.warn('  href   ');
          }
        }
        return;
      }
      
      console.warn('     :', page);
      return;
    } 
    
    console.warn('    :', typeof page, page);
  }, [book?.id, saveBookmark]);

  const handleSettingsChange = useCallback((newSettings) => {
    console.log(' ReaderView  :', newSettings);
    setReadingSettings(prev => {
      const updated = { ...prev, ...newSettings };
      console.log('  :', updated);
      return updated;
    });
  }, []);



  const handleTextSelection = (selectedText, position) => {
    // startChat    AI  
    if (position && position.startChat) {
      setShowChat(true);
    }
  };

  //   
  const handleResizeStart = (e) => {
    setIsResizing(true);
    const startX = e.clientX;
    const startWidth = chatSidebarWidth;

    const handleMouseMove = (e) => {
      const deltaX = startX - e.clientX;
      const newWidth = Math.max(400, Math.min(800, startWidth + deltaX));
      setChatSidebarWidth(newWidth);
    };

    const handleMouseUp = () => {
      setIsResizing(false);
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  //   
  const handleRendererError = (error) => {
    console.error('  :', error);
    
    //    ServiceWorker      
    if (isRefreshRecovery && (
      error.message.includes('EPUB data not found') ||
      error.message.includes('    ') ||
      error.message.includes('ServiceWorker') ||
      error.message.includes(' URL')
    )) {
      console.log('   :    ...');
      
      setError('     ...');
      
      //   
      let retryAttempt = 0;
      const maxRetries = 3;
      
      const attemptRecovery = async () => {
        retryAttempt++;
        console.log(`   ${retryAttempt}/${maxRetries}`);
        
        try {
          if (retryAttempt === 1) {
            // 1: ServiceWorker 
            console.log(' 1 : ServiceWorker ');
            if ('serviceWorker' in navigator) {
              await navigator.serviceWorker.getRegistrations().then(registrations => {
                registrations.forEach(registration => registration.unregister());
              });
              await navigator.serviceWorker.register('/epub-proxy-worker.js');
              await navigator.serviceWorker.ready;
            }
            
            // 3   
            setTimeout(() => {
              if (retryAttempt === 1) {
                window.location.reload();
              }
            }, 3000);
            
          } else if (retryAttempt === 2) {
            // 2:    
            console.log(' 2 :    ');
            if ('caches' in window) {
              const cacheNames = await caches.keys();
              await Promise.all(
                cacheNames.map(cacheName => caches.delete(cacheName))
              );
            }
            
            // 5   
            setTimeout(() => {
              if (retryAttempt === 2) {
                window.location.reload();
              }
            }, 5000);
            
          } else {
            // 3:  
            console.log(' 3 :  ');
            
            // localStorage 
            Object.keys(localStorage).forEach(key => {
              if (key.includes('reader') || key.includes('epub')) {
                localStorage.removeItem(key);
              }
            });
            
            // sessionStorage 
            sessionStorage.clear();
            
            //  
            setTimeout(() => {
              window.location.reload();
            }, 2000);
          }
          
        } catch (recoveryError) {
          console.error(` ${retryAttempt}  :`, recoveryError);
          
          if (retryAttempt < maxRetries) {
            setTimeout(attemptRecovery, 3000);
          } else {
            setError('  .     .');
          }
        }
      };
      
      //     
      setTimeout(attemptRecovery, 1000);
      return;
    }
    
    // ReactReader EPUB     TextRenderer  
    if (error.message && (
      error.message.includes('ReactReader EPUB   ') ||
      error.message.includes('TextRenderer  ') ||
      error.message.includes('package') ||
      error.message.includes('Cannot read properties of undefined')
    )) {
      console.log(' ReactReader  - TextRenderer  ');
      
      // book  TextRenderer 
      if (book) {
        const modifiedBook = {
          ...book,
          type: 'text', //  text  
          useTextRenderer: true //  
        };
        
        setBook(modifiedBook);
        setError(null); //   
        
        console.log(' TextRenderer  ');
        return;
      }
    }
    
    setError('  : ' + error.message);
  };

  //     
  // book?.type, rendererType, book.hasFile 
  
  if (loading) {
    return (
      <Container>
        <LoadingSpinner 
          message="  ..." 
          subtext="IndexedDB    "
        />
      </Container>
    );
  }

  if (error || (!loading && !book)) {
    return (
      <Container>
        <ErrorContainer>
          <h3>     </h3>
          <p>{error || '   .'}</p>
          
          {/*        */}
          {error && (
            error.includes('EPUB data not found') ||
            error.includes('    ') ||
            error.includes('ServiceWorker') ||
            error.includes(' URL') ||
            error.includes(' ') ||
            error.includes(' ')
          ) && (
            <div style={{ marginTop: '20px', textAlign: 'center' }}>
              <div style={{ 
                background: '#e3f2fd', 
                border: '1px solid #bbdefb', 
                borderRadius: '8px', 
                padding: '20px', 
                marginBottom: '20px' 
              }}>
                <h4 style={{ color: '#1565c0', margin: '0 0 10px 0' }}>
                      
                </h4>
                <p style={{ color: '#1976d2', margin: '0', fontSize: '0.9rem', lineHeight: '1.4' }}>
                       .<br/>
                      .
                </p>
                
                {error.includes(' ') && (
                  <div style={{ marginTop: '15px' }}>
                    <div style={{ 
                      width: '100%', 
                      height: '4px', 
                      background: '#e0e0e0', 
                      borderRadius: '2px',
                      overflow: 'hidden'
                    }}>
                      <div style={{
                        width: '100%',
                        height: '100%',
                        background: 'linear-gradient(90deg, #4CAF50, #45a049)',
                        animation: 'progress 2s ease-in-out infinite'
                      }} />
                    </div>
                    <style>{`
                      @keyframes progress {
                        0% { transform: translateX(-100%); }
                        100% { transform: translateX(100%); }
                      }
                    `}</style>
                    <p style={{ 
                      fontSize: '0.8rem', 
                      color: '#666', 
                      margin: '10px 0 0 0' 
                    }}>
                       ...  .
                    </p>
                  </div>
                )}
              </div>
              
              <div style={{ display: 'flex', gap: '10px', justifyContent: 'center', flexWrap: 'wrap' }}>
                <button 
                  onClick={() => window.location.reload()}
                  style={{
                    padding: '12px 24px',
                    backgroundColor: '#4CAF50',
                    color: 'white',
                    border: 'none',
                    borderRadius: '8px',
                    cursor: 'pointer',
                    fontSize: '0.9rem',
                    fontWeight: '500'
                  }}
                >
                     
                </button>
                
                <button 
                  onClick={handleBack}
                  style={{
                    padding: '12px 24px',
                    backgroundColor: '#f8f9fa',
                    color: '#666',
                    border: '1px solid #e0e0e0',
                    borderRadius: '8px',
                    cursor: 'pointer',
                    fontSize: '0.9rem',
                    fontWeight: '500'
                  }}
                >
                    
                </button>
              </div>
              
              <div style={{ 
                marginTop: '20px', 
                padding: '15px', 
                background: '#fff3e0', 
                border: '1px solid #ffcc02',
                borderRadius: '8px',
                fontSize: '0.85rem',
                color: '#e65100'
              }}>
                <strong> :</strong><br/>
                       <br/>
                    (10-30 )<br/>
                      
              </div>
            </div>
          )}
          
          {/*      */}
          {(!error || !(
            error.includes('EPUB data not found') ||
            error.includes('    ') ||
            error.includes('ServiceWorker') ||
            error.includes(' URL') ||
            error.includes(' ') ||
            error.includes(' ')
          )) && (
            <div style={{ marginTop: '20px' }}>
              <button 
                onClick={handleBack}
                style={{
                  padding: '12px 24px',
                  backgroundColor: '#f8f9fa',
                  color: '#666',
                  border: '1px solid #e0e0e0',
                  borderRadius: '8px',
                  cursor: 'pointer',
                  fontSize: '0.9rem',
                  fontWeight: '500'
                }}
              >
                  
              </button>
            </div>
          )}
        </ErrorContainer>
      </Container>
    );
  }

  return (
    <Container>
      <Sidebar isOpen={showTOC}>
        {book && (
          <TableOfContents
            book={book}
            onPageChange={handlePageChange}
            onClose={() => setShowTOC(false)}
          />
        )}
      </Sidebar>

      <MainContent>
        <Header>
          <HeaderLeft>
            <BackButton onClick={handleBack} title=" ">
              <FiArrowLeft size={20} />
            </BackButton>
            <BookTitle>{book?.title || ' '}</BookTitle>
          </HeaderLeft>

          <HeaderRight>
            <HeaderButton
              active={showTOC}
              onClick={() => setShowTOC(!showTOC)}
              title=""
            >
              <FiBookmark size={18} />
            </HeaderButton>
            
            <HeaderButton
              active={showSettings}
              onClick={() => setShowSettings(!showSettings)}
              title=" "
            >
              <FiSettings size={18} />
            </HeaderButton>
            
            <HeaderButton
              active={showChat}
              onClick={() => setShowChat(!showChat)}
              title="AI "
            >
              <FiMessageSquare size={18} />
            </HeaderButton>
            

          </HeaderRight>
        </Header>

        <ReadingArea>
          <ContentArea>
            {(book?.type === 'epub' && !book?.useTextRenderer) ? (
              <ReactReaderRenderer
                ref={reactReaderRef}
                key={`react-reader-${book?.id}`}
                book={book}
                settings={readingSettings}
                onPageChange={handlePageChange}
                onTextSelection={handleTextSelection}
                onError={handleRendererError}
                pendingBookmarkLocation={pendingBookmarkLocation}
                onBookmarkRestored={() => setPendingBookmarkLocation(null)}
              />
            ) : (
              <TextRenderer
                book={book}
                settings={readingSettings}
                onPageChange={handlePageChange}
                onTextSelection={handleTextSelection}
                onToggleTOC={() => setShowTOC(!showTOC)}
              />
            )}
            
            {showSettings && (
              <ReadingSettings
                settings={readingSettings}
                onSettingsChange={handleSettingsChange}
                onClose={() => setShowSettings(false)}
              />
            )}
          </ContentArea>

          <ChatSidebar 
            isOpen={showChat} 
            width={chatSidebarWidth}
            isResizing={isResizing}
          >
            {showChat && (
              <ResizeHandle 
                onMouseDown={handleResizeStart}
                title="  "
              />
            )}
            <AIChat
              book={book}
              onClose={() => setShowChat(false)}
            />
          </ChatSidebar>
        </ReadingArea>
      </MainContent>
    </Container>
  );
};

export default ReaderView; 